window.detalleFuente = function () {
    // Ocultar el botón btnMostrar
    var botonElement = document.getElementById("btnDetalleFuente");


    var detalleElement = document.getElementById("detalleFuente");

    // Si el detalle está oculto
    if (detalleElement.style.visibility === "visible") {
        detalleElement.style.visibility = "hidden";         // Mostrar el detalle de la fuente
        botonElement.innerHTML = "Mostrar Fuente";          // Cambiar texto del botón
    } else {
        detalleElement.style.visibility = "visible";        // Mostrar el detalle de la fuente
        botonElement.innerHTML = "Ocultar Fuente";          // Cambiar texto del botón
    }
}   